<script setup>

import { ref, computed, watchEffect } from 'vue'
import CarouselComponent from "../app-ui/CarouselComponent.vue"
import { transitionEnter, transitionLeave } from "@/utils/hsDetailsTransition";


const props = defineProps({
    canShow: {
        type: Boolean,
        default: true
    },

    hsData: {
        type: Object,
        default: null
    },

    typesColors: {
        types: Object,
    }
});


const typeColor = ref("#cecece");

const items = ref([
    {
        url: "",
        title: "Plan 3D",
        desc: ""
    },
    {
        url: "",
        title: "Plan 2d",
        desc: ""
    }
]);


watchEffect(() => {
    const apptName = props.hsData.nom;
    items.value[0].url = `assets/images/lots/3d/${apptName}.jpg`;
    items.value[1].url = `assets/images/lots/2d/${apptName}.jpg`;

});





const appt = computed(() => {
    const a = props.hsData.appt;
    const prefix = (a <= 9) ? "0" : "";
    return prefix + a;
});
const bloc = computed(() => {
    const a = props.hsData.bloc;
    const prefix = (a <= 9) ? "0" : "";
    return prefix + a;
});

const body__infos = ref({
    Surface: "",
    Type: "",
    Etage: "",
    Terrasse: "",
    Code: "",
});

watchEffect(() => {
    const hsInfo = props.hsData;
    body__infos.value = {
        Surface: hsInfo.surface + "m²",
        Type: hsInfo.type,
        Etage: hsInfo.etage,
        Terrasse: hsInfo.terrasse + "m²",
        Code: hsInfo.code_ref,
    }

    const colorIndex = hsInfo.type;
    typeColor.value = props.typesColors[colorIndex]
});




function onEnter(el) {
    transitionEnter(el);
}

function onLeave(el, done) {
    transitionLeave(el, done);
}

</script>



