class InputSystem {
    constructor(rect , naturalSizes , zoomOptions , dragOptions) {
        this.rect = rect;
        this.drag = new Drag(rect , dragOptions);
        this.zoom = new Zoom(rect , naturalSizes , zoomOptions);
        
        this.init();
    }

    _hasTouch() {
        return (('ontouchstart' in window));
    }

    init() {
        
        if(this._hasTouch()) {
            console.log("has touch");
            //touch events listeners
            this._touchListeners();
        }else{
            // mouse events
            this._mouseListeners();
            /**
             * zoom control events
             */
            this._pointerZoomEvent();
        }

        this.rect.addEventListener('wheel', (e) => {
            e.preventDefault();
            this.zoom._wheelZoom(e);
        });

    }

    _mouseListeners(){
        this.rect.onmousedown = e => {
            //console.log("mouse Start");
            this._mouseDown(e);
            
        }

        this.rect.onmousemove = e => {
            this._mouseMove(e);
        }

        this.rect.onmouseup = (e) => {
            this._mouseUp(e);
        }
    }

    _touchListeners(){
        this.rect.addEventListener("touchstart" , (e) => {
            e.preventDefault();
            this._touchStart(e);
        });

        this.rect.addEventListener("touchmove" , (e) => {
            e.preventDefault();
            this._touchMove(e);
        } , { passive: false }); // without !passive event isn't preventing default behavior in ios safari 15+

        this.rect.addEventListener("touchend" , (e) => {
            this._touchEnd(e);
        });
    }

    
    _pointerZoomEvent(){
        
        //pointer events for zoom
        this.rect.addEventListener("pointerdown" , (e) => {
            this.zoom.pointerStart(e);
        });

        this.rect.addEventListener("pointermove" , (e) => {
            e.preventDefault();
            this.zoom.pointerMove(e);
        });

        this.rect.addEventListener("pointerup" , (e) => {
            this.zoom.pointerEnd(e);
            
        });

        this.rect.addEventListener("pointercancel" , (e) => {
            this.zoom.pointerEnd(e);
           
        });

        this.rect.addEventListener("pointerout" , (e) => {
            this.zoom.pointerEnd(e);
        });

        this.rect.addEventListener("pointerleave" , (e) => {
            this.zoom.pointerEnd(e);
        });
    }


    


    //#region Mouse interactions
    _mouseDown(e){
        this.drag._startEvent(e.clientX);
    }
    _mouseMove(e){
        
        if(this.zoom.isZooming){
            this.drag.clear();
            return;
        } 
        
        this.drag.mouseMove(e);
    }
    _mouseUp(e){
        this.drag.mouseUp(e);
    }
    //#endregion



    //#region Touch interactions
    _touchStart(e) {
        const touches = e.changedTouches;
        this.zoom.touchStart(e);
        this.drag._startEvent(touches[0].clientX);
        
    }
    _touchMove(e) {
        this.zoom.touchMove(e);
        if(this.zoom.isZooming){
            this.drag.clear();
            return;
        } 
        this.drag.touchMove(e);
        
        
    }
    _touchEnd(e) {
        this.zoom.touchEnd(e);
        this.drag._endEvent();
    }
    //#endregion
}
















































class Drag {
    /**
     * control dragging events
     * @param {HTMLCanvasElement} rect - canvas events capturer
     * @param {object} dragOptions - drag options
     */
     constructor(rect , dragOptions) {
        this.rect = rect;

        // drag parameters
        this._sensitivity = dragOptions.sensitivity; // drag sensitivity, heigher value for faster animation
        this._threshold = dragOptions.threshold; // drag differnce to reach for a step
        this._throwSens = dragOptions.throwSens; // drag throwing sensitivity , heigher value for faster animation
        this._maxVelocity = dragOptions.maxVelocity; 
        /**
         * when dragging with height velocity, we capture the number of frames to be played
         * heigher value for faster animation
         */
        this._maxStepsCaptures = 50; 
        this.velocity = undefined;

        //this._dragWait = 0;


        this.isDragging = false;
        this.reverse = true;

        this._prevX = undefined;
        this._prevT = undefined; // the previus time when dragging, useful to calculate the velocity
        this._lastDiff = undefined; // the last drag difference
        this._bounding;

        this._waitTimout;

        this.init();
    }

    /**
     * get the canvas bounding rect
     */
    get _bounding(){
        return this.rect.getBoundingClientRect();
    }

    init() {
        this.clear();
    }

    /**
     * clear the dragging params
     */
    clear(){
        this.isDragging = false;
        this._prevX = undefined;
        this._prevT = undefined;
        this._lastDiff = undefined;
        clearTimeout(this._waitTimout);
    }

    //#region Mouse interactions
    mouseDown(e){
        this._startEvent(e.clientX);
    }

    mouseMove(e){
        
        if(!this.isDragging) return;
        this._rotate(e.clientX);
    }

    mouseUp(){
        this._endEvent();
    }
    //#endregion

    //#region Touch interactions
    touchStart(e) {
        const touches = e.touches;
        if(touches.length > 1) {
            this.clear();
            return
        }
        this._startEvent(touches[0].clientX);
    }

    touchMove(e) {
        if(!this.isDragging) return;
        const touches = e.touches;
        if(touches.length > 1) {
            this.clear();
            return
        }
        
        this._rotate(touches[0].clientX);
    }

    touchEnd() {
        this._endEvent();
    }
    //#endregion


    //#region Drag setup
    _startEvent (clientX){
        this.isDragging = true;
        this._prevX = this._getCurrentX(clientX);
        this._prevT = Date.now();
        clearTimeout(this._waitTimout);
    }
    _endEvent (){
        this.isDragging = false;
        this._throw();
        this._lastDiff = undefined;
    }
    //#endregion



    /**
     * animate when dragging
     * @param {Number} clientX - x coordinate of the click/touch event
     */
    async _rotate(clientX){
        
        
        //get the current x of the interaction
        const currentX = this._getCurrentX(clientX);
        //calculate the difference between the last pos and the current pos
        this._lastDiff = Math.round(currentX - this._prevX);
        const new_t = Date.now();
        const elapsed = new_t - this._prevT;
        //this.velocity = Math.sqrt(Math.pow(this._lastDiff , 2))/elapsed;
        this.velocity = Math.abs(this._lastDiff/elapsed);

        //compare the absolute difference and the mouse threshold
        if(Math.abs(this._lastDiff) > this._threshold) {
            
            //check if direction is reversed
            this.reverse = this._lastDiff > 0;

            /**
             * play animation based on the delta x movement,
             * await the acceleration interval,
             * then we drag the next iteration
             */

            const condition = Math.floor(this.velocity * this._sensitivity);
            //console.log(condition);
            for (let i = 0; i <= condition ; i++) {

                
                // acceleration timer
                await this._wait(this.velocity);

                /**
                 * bind the OnDrag Event
                 *  
                 */
                this.rect.dispatchEvent(new CustomEvent("OnDrag", {
                    bubbles: false,
                    // detail :bool , indicate the drag direction (!reversed : right)
                    detail : this.reverse
                }));

            }

            //save the current params
            this._prevX = currentX;
            this._prevT = new_t;
        }
    }

    /**
     * throwing system with acceleration 
     * @returns null
     */
    async _throw(){
        const absDiff = Math.abs(this._lastDiff);
        if (this.isDragging || this._lastDiff === undefined || absDiff < this._threshold) {
            this.clear();
            return;
        }

        // get the direction
        const reverse = this._lastDiff > 0;

        // saved captures when dragging , based on acceleration and mapped to the max captures
        let captures = Math.round( Math.abs(this.velocity) ) * this._throwSens;
        captures = captures > this._maxStepsCaptures ? this._maxStepsCaptures : captures;
        
        // mapping the velocity to the max velocity
        this.velocity = this.velocity > this._maxVelocity ? this._maxVelocity : this.velocity;
        // inverting the value based on the max velocity
        this.velocity = this._maxVelocity - this.velocity;

        for (let i = 0; i < captures ; i++) {
            if (this.isDragging) {
                this.clear();
                break;
            }
            
            await this._wait(this.velocity);
            this.rect.dispatchEvent(new CustomEvent("OnDrag", {
                bubbles: false,
                detail : reverse
            }));
            const ease = this._easeOutSine(i/captures);
            this.velocity += ease * this._sensitivity;
        }
    }
    
    /**
     * Sin ease function 
     * @param {Number} x value from 0 to 1
     * @returns easing value from 0 to 1
     */
    _easeOutSine(x) {
        return Math.sin((x * Math.PI) / 2);
    }


    /**
     * timer with promise
     * @returns {Promise} the wait promise
     */
     _wait(ms) {
        const p = new Promise( (resolve) => {
            this._waitTimout = setTimeout( () => {
                resolve();
            } , ms);
        } );
        
        return p;
    }


    /**
     * return the valid click or touch 
     * @param {Number} clientX the clientX of the interaction
     * @returns {Number} valid click/touch in the rect
     */
    _getCurrentX(clientX) {
        return clientX - this._bounding.left;
    }

}

































































class Zoom {

    /**
     * 
     * @param {*} rect - rect containing events
     * @param {Object} naturalSizes - natural size of images
     * @param {Number} naturalSizes.width - natural images width 
     * @param {Number} naturalSizes.height - natural images height 
     * @param {Object} options - zoom params
     */
    constructor(rect , naturalSizes , options){
        this.rect = rect;
        this._naturalSizes = naturalSizes;
        this.options = options;

        //cache Pointers event
        this.evCache = [];
        this.prevDiff = -1;

        //cache Touches event
        this.prevTouchDiff = -1;

        this.isZooming = false;

        this.zoomPoints = -1;

        this.init();
    }

    init() {

        this.defaultOptions = {
            scaleFactor : 0.03,
            currentScale : 0.5,
            minScale : 0.26,
            maxScale : 1,
            scaleMargins : 0.2,
            zoomInterval : 16.66
        };
        this.setOptions(this.options);
        this.setMaxZoom();

        
    }

    /**
     * set the zoom options
     * @param {Object} options - zoom options
     * @param {Number} options.scaleFactor 
     * @param {Number} options.currentScale
     * @param {Number} options.minScale 
     * @param {Number} options.maxScale
     * @public
     */
    setOptions(options) {

        if (typeof(options) == "undefined") {

            this.options = this.defaultOptions;

        }else {
            Object.keys(options).forEach((opt) => {

                if(typeof(this.options[opt]) !== "undefined") {
                    this.options[opt] = options[opt]
                }
                
            })
        }

        this.currentScale = this.options.initScale;
        this.scaleFactor = this.options.scaleFactor;
        this.minScale = this.options.minScale;
        this.maxScale = this.options.maxScale;
        this.scaleMargins = this.options.scaleMargins;

    }


    //#region Touch interactions
    touchStart(e) {
        if(e.touches.length === 2) {
            this.isZooming = true;
        }
        
    }
    touchMove(e) {
        if(e.touches.length === 2) {
            const curDiff = Math.abs(e.touches[0].clientX - e.touches[1].clientX);
            
            if (this.prevTouchDiff > 0) {

                let zoomIn;

                if(curDiff > this.prevTouchDiff) {
                    zoomIn = true;
                }

                if(curDiff < this.prevTouchDiff) {
                    zoomIn = false;
                }

                const pinchCenter = this._getPinchCenter(
                    e.touches[1].clientX, 
                    e.touches[0].clientX,
                    e.touches[1].clientY,
                    e.touches[0].clientY,
                );

                this._pinchZoom(zoomIn , pinchCenter.xPoint , pinchCenter.yPoint);
            }
            // Cache the distance for the next move event
            this.prevTouchDiff = curDiff;
        }
        
    }
    touchEnd(e) {
        this.isZooming = false;
        // If the number of pointers down is less than two then reset diff tracker
        if (e.changedTouches.length < 2) {
            this.prevTouchDiff = -1;
        }
        
    }
    //#endregion




    
    //#region Pointer interactions
    pointerStart(e) {
        this.evCache.push(e);
        
    }

    pointerMove(e) {

        // Find this event in the cache and update its record with this event
        const index = this.evCache.findIndex((cachedEv) => cachedEv.pointerId === e.pointerId);
        this.evCache[index] = e;

        // If two pointers are down, check for pinch gestures
        if (this.evCache.length === 2) {
            // Calculate the distance between the two pointers
            const curDiff = Math.abs(this.evCache[0].clientX - this.evCache[1].clientX);

            if (this.prevDiff > 0) {

                let zoomIn = true;

                if(curDiff > this.prevDiff) {
                    zoomIn = true;
                }

                if(curDiff < this.prevDiff) {
                    zoomIn = false;
                }

                const pinchCenter = this._getPinchCenter(
                    this.evCache[1].clientX, 
                    this.evCache[0].clientX,
                    this.evCache[1].clientY,
                    this.evCache[0].clientY,
                );

                this._pinchZoom(zoomIn , pinchCenter.xPoint , pinchCenter.yPoint);
            }
            // Cache the distance for the next move event
            this.prevDiff = curDiff;
            
        } 
    }

    pointerEnd() {
        this.isZooming = false;
        // Remove this pointer from the cache
        //this._removeEvent(e);
        this.evCache = [];
        //console.log(e);
        // If the number of pointers down is less than two then reset diff tracker
        /* if (this.evCache.length < 2) {
            this.prevDiff = -1;
        } */
    }

    _getPinchCenter(x0 , x1 , y0 , y1) {
        const diffX = x1 - x0;
        const diffY = y1 - y0;
        const xPoint = x0 + diffX * 0.5;
        const yPoint = y0 + diffY * 0.5;
        return {xPoint , yPoint};
    }

    _removeEvent(e) {
        // Remove this event from the target's cache
        const index = this.evCache.findIndex((cachedEv) => cachedEv.pointerId === e.pointerId);
        this.evCache.splice(index, 1);
      }
    //#endregion

    

    /**
     * 
     * @param {Boolean} ZoomIn 
     * @returns null
     */
    _pinchZoom(ZoomIn , xPoint , yPoint){

        if(!this._canZoom(ZoomIn)) return;
        this.isZoomIn = ZoomIn;
        this.isZooming = true;
        this._limitScale();

        this._setZoomPoints(xPoint , yPoint);
        
        this.rect.dispatchEvent(new CustomEvent("OnZoom", {
            bubbles: false
        }));
    }

    _wheelZoom(e){

        if(!this._canZoom(e.deltaY < 0)) return;
        this.isZoomIn = e.deltaY < 0;

        this._limitScale();

        const xPoint = e.clientX - this.rect.getBoundingClientRect().left;
        const yPoint = e.clientY - this.rect.getBoundingClientRect().top;
        this._setZoomPoints(xPoint , yPoint);
        
        this.rect.dispatchEvent(new CustomEvent("OnZoom", {
            bubbles: false
        }));
        
    }

    _canZoom(zoomIn) {
        const timestamp = Date.now();
        if (this.zoomTimeStart === undefined) {
            this.zoomTimeStart = timestamp;
        }
        const elapsed = timestamp - this.zoomTimeStart;
    
        if(elapsed <= this.options.zoomInterval) { //framerate simulation must refactor 
            return false;
        }
        this.zoomTimeStart = timestamp;
        
        if(zoomIn) {

            //zoom in
            if(this.currentScale >= this.maxScale) return false
            this.currentScale += this.scaleFactor;
        }else{
            //zoomOut
            if(this.currentScale <= this.minScale) return false;
            this.currentScale -= this.scaleFactor;
        }

        return true;
    }



    _limitScale() {
        if(this.currentScale >= this.maxScale || this.currentScale <= this.minScale) {
            this.currentScale = Math.min(Math.max(this.currentScale, this.minScale), this.maxScale);
        }
        
    }
    
    
    setMaxZoom() {
        const natWidth = this._naturalSizes.width;
        let scale = window.innerWidth / natWidth;
        scale += this.scaleMargins;
        if(scale >= 1){
            
            this.currentScale = this.maxScale;
            return;
        } 

        if(scale <= this.minScale){
            this.maxScale = this.minScale;
            this.currentScale = this.minScale;
            return;
        } 

        
        this.maxScale = scale;
        if(scale < this.currentScale ){
            this.currentScale = scale;
        }
        
    }

    _setZoomPoints(x , y){
        if(this._canZoomInPoint()) {
            this.zoomPoints = {
                x : x, 
                y : y
            };
        }else{
            this.zoomPoints = -1;
        }
    }

    _canZoomInPoint() {
        const scaleAmount = 0.4; // scale amount to apply points zooming
        const rayScale = this.minScale + ((this.maxScale - this.minScale) * scaleAmount);
        return this.currentScale > rayScale;
    }
      
      

}


export {InputSystem}