<script setup>

import { ref, watchEffect, onMounted } from "vue";
import { Tooltip } from "bootstrap";
import { gsap } from "gsap";

const bsPlacement = "top";

const props = defineProps({
    stageCount: {
        type: Number,
        validator(value) {
            return value > 0
        },
        default: 1
    },
});

const emits = defineEmits([
    "onSetLevel",
    "onPlay",
    "onStep",
    "onShowFilter"
])


const isOpen = ref(false);

const activeStageIndex = ref(0);

const stages = ref([]);




watchEffect(() => {
    const count = props.stageCount;
    for (let i = 0; i < count; i++) {
        stages.value[i] = {
            index: i,
            title: i,
            tooltipTitle: "Etage " + i,
            isActive: activeStageIndex.value === i
        }
    }

    stages.value[0].title = "RDC";
    stages.value[0].tooltipTitle = "RDC";

    if (count > 1) {
        stages.value[count - 1].title = "Toit";
        stages.value[count - 1].tooltipTitle = "Toit";
    }
});

function stageClick(index) {
    activeStageIndex.value = index;
    emits('onSetLevel', index);
}



const showBtn = ref(null);
const sidebar = ref(null);

function showMenu() {

    isOpen.value = true;
    gsap.set(sidebar.value, {
        autoAlpha: 0,
        y: "100%"
    })
    gsap.to(sidebar.value, {
        autoAlpha: 1,
        y: 0,
        duration: .3
    })
}

function closeMenu() {
    gsap.to(sidebar.value, {
        autoAlpha: 0,
        y: "100%",
        duration: .2,
        onComplete: () => {
            isOpen.value = false
        }
    })
}



onMounted(() => {
    const tooltipTriggerList = document.querySelectorAll('[data-bs-toggle="tooltip"]');
    [...tooltipTriggerList].map(tooltipTriggerEl => new Tooltip(tooltipTriggerEl, {
        trigger: "hover",
        customClass: "custom-tooltip",
    }));
});

</script>




