import {createRouter , createWebHistory} from 'vue-router'


import HomePage from "@/pages/HomePage";
import GaleriePage from "@/pages/GaleriePage";
import LocalisationPage from "@/pages/LocalisationPage";
import VideoPage from "@/pages/VideoPage";



const routes = [
    { path: '/', name : 'home' , component: HomePage },
    { path: '/galerie',  name : 'galerie' , component: GaleriePage },
    { path: '/localisation', name : 'localisation' , component: LocalisationPage },
    { path: '/video', name : 'video' , component: VideoPage },
    { path: '/:pathMatch(.*)*', name: 'home', component: HomePage}
]

const router = createRouter({
    history: createWebHistory(),
    linkActiveClass: "active",
    routes,
})

export default router;