<template>

    <div class="container-fluid">
        <div class="row align-items-center position-relative">
            <div id="carousel-template" class="carousel carousel-dark slide" data-bs-ride="false" data-bs-touch="false">

                <div class="carousel-inner d-flex align-items-center">
                    <div class="carousel-item" v-for="(item, index) in itemsData" :key="index"
                        :class="{ 'active': (index == 0) }">
                        <img :src="getImagePath(item.url)" class="d-block w-100" :alt="item.title" loading="lazy">
                        <div class="carousel-caption d-block d-md-block">
                            <h5 class="p-0 m-0">{{ item.title }}</h5>
                            <p class="p-0 m-0">{{ item.desc }}</p>
                        </div>
                    </div>
                </div>

                <div class="carousel-indicators">
                    <button v-for="(item, index) in itemsData" :key="index" type="button"
                        data-bs-target="#carousel-template" :data-bs-slide-to=index :class="{ 'active': (index == 0) }"
                        aria-current="true" aria-label="Slide {{index}}">
                    </button>
                </div>

                <button class="carousel-control-prev" type="button" data-bs-target="#carousel-template"
                    data-bs-slide="prev">
                    <span class="" aria-hidden="true">
                        <svg xmlns="http://www.w3.org/2000/svg" width="60%" height="60%" fill="currentColor"
                            class="bi bi-chevron-left" viewBox="0 0 16 16">
                            <path fill-rule="evenodd"
                                d="M11.354 1.646a.5.5 0 0 1 0 .708L5.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z" />
                        </svg>
                    </span>
                    <span class="visually-hidden">Previous</span>
                </button>

                <button class="carousel-control-next" type="button" data-bs-target="#carousel-template"
                    data-bs-slide="next">
                    <span class="" aria-hidden="true">
                        <svg xmlns="http://www.w3.org/2000/svg" width="60%" height="60%" fill="currentColor"
                            class="bi bi-chevron-right" viewBox="0 0 16 16">
                            <path fill-rule="evenodd"
                                d="M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z" />
                        </svg>
                    </span>
                    <span class="visually-hidden">Next</span>
                </button>
            </div>
        </div>
    </div>


</template>



<script setup>

defineProps({
    itemsData: Object,
    colorType: {
        type: String,
        default: "#000"
    }
});

function getImagePath(path, placeholder = true) {
    let src = "";
    try {
        src = require(`@/${path}`);
    } catch (error) {
        if (!placeholder) {
            src = "https://via.placeholder.com/512x256.png/fff/ccc?text=Image not found";
        } else {
            src = require("@/assets/images/lots/3d/appt11_2_bloc4.jpg");
        }
    }
    return src;
}


</script>





