    $color-type : v-bind(colorType);
    $ind-bg : rgb(173, 173, 173);
    $ind-size : 1em;
    $ind-small-size : .5em;

    position: initial;

    img {
        max-height: 80vh;
    }

    .carousel-indicators {


        @mixin type-styles {
            border-color: $color-type;

            &::after {
                background-color: $color-type;
            }
        }

        button {
            position: relative;
            width: $ind-size;
            height: $ind-size;
            background-color: transparent;
            border: .1em solid $ind-bg;
            border-radius: 50%;
            margin: 0 .6em;
            opacity: 1;

            &::after {
                content: '';
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                width: $ind-small-size;
                height: $ind-small-size;
