<template>

    <div class="parent position-relative p-0 m-0 border border-0">

        <LoaderComponent :show="showLoader" :percentLoaded="percentLoaded"></LoaderComponent>

        <HotspotDetails :types-colors="typesColors" :canShow="showHsDetails" :hsData="hsData"
            @onCloseDetails="showHsDetails = false">
        </HotspotDetails>

        <OrbitalMenu :stage-count="stageCount" @onPlay="play" @onStep="step" @onSetLevel="setLevel"
            @onShowFilter="showFilter">
        </OrbitalMenu>

        <div class="orbital-canvas" id="orbital">
            <PopoverComponent :types-colors="typesColors"></PopoverComponent>
        </div>

        <FilterComponent :min="49" :max="66" :step="0.1" :gap="0" :showToast="showToast" @onCloseToast="closeFilter"
            @onSurfaceChanged="surfaceChanged">
        </FilterComponent>

    </div>

</template>



<script setup>

import { ref, onMounted, onUnmounted } from "vue";

import { Orbital } from "@/canvas-modules/orbital";
import PopoverComponent from "./PopoverComponent.vue";
import FilterComponent from "./FilterComponent.vue";
import LoaderComponent from "./LoaderComponent.vue";
import HotspotDetails from "./HotspotDetails.vue";
import OrbitalMenu from "./OrbitalMenu.vue";



let orbital = null,

    showToast = ref(false),

    showLoader = ref(true),

    showHsDetails = ref(false),

    hsData = ref({}),

    stageCount = ref(1),

    typesColors = ref({}),

    percentLoaded = ref(0);


function init() {
    orbital = new Orbital("orbital");
    orbital.init();

    stageCount.value = orbital.getStageCount();

    typesColors.value = orbital.getTypesColors();

    window.addEventListener("OnLoadingChange", onLoadingChange);
    window.addEventListener("OnAppReady", onAppReady);
    window.addEventListener("OnHSClick", onHSClick);
}



function setLevel(level) {
    orbital.setLevel(level);
}

function play(play) {
    if (play) orbital.playOrbital();
    else orbital.stopOrbital();
}

function step(reverse) {
    if (reverse)
        orbital.prev();
    else
        orbital.next()
}

function showFilter() {
    showToast.value = true;
}

function closeFilter() {
    showToast.value = false;
}

function surfaceChanged(surface) {
    orbital.filterHSByPrice(surface);
}

function clearEvents() {
    window.removeEventListener("OnLoadingChange", onLoadingChange);
    window.removeEventListener("OnAppReady", onAppReady);
    window.removeEventListener("OnHSClick", onHSClick);
    window.removeEventListener("onCloseToast", closeFilter);
}
/**
 * Events functions
 */

function onLoadingChange() {
    percentLoaded.value = orbital.getPercentLoaded();
}

function onAppReady() {
    showLoader.value = false;
}

function onHSClick(e) {
    const detail = e.detail;
    const region = detail.region;
    if (region === undefined) return;

    hsData.value = region;
    showHsDetails.value = true;
}



onMounted(() => {
    init();
})

onUnmounted(() => {
    clearEvents();
    orbital.unmountOrbital();
})

</script>


<style lang="scss">
.orbital-canvas {
    position: relative;
    height: 100%;
    padding: 0 !important;
    margin: 0 !important;
    border: none !important;
}


.parent {
    height: 100vh;
}
</style>
