<template>

    <div v-show="showPopover" class="position-absolute" id="popper">
        <div class="card shadow border border-0">
            <div class="card-header p-0 overflow-hidden">
                <div class="color-type"></div>
                <div class="container-fluid">
                    <div class="row align-items-center bg-dark text-white py-2 text-uppercase text-center">
                        <div class="col-4 p-0">
                            <p class="p-0 m-0">Appt-{{ appt }}</p>
                        </div>
                        <div class="col-4 p-0">
                            <p class="p-0  m-0">Bloc-{{ bloc }}</p>
                        </div>
                        <div class="col-4 p-0">
                            <p class="p-0  m-0">Etage-{{ etage }}</p>
                        </div>
                    </div>
                </div>

            </div>
            <div class="card-body">
                <div class="container-fluid">

                    <div class="row align-items-center p-1">
                        <div class="col-4">
                            <span>Surface</span>
                        </div>
                        <div class="col-8">
                            <p>{{ infoHt.surface }} M<sup>2</sup></p>
                        </div>
                    </div>

                    <div class="row align-items-center p-1">
                        <div class="col-4">
                            <span>Type</span>
                        </div>
                        <div class="col-8">
                            <p>{{ infoHt.type }}</p>
                        </div>
                    </div>

                    <div class="row align-items-center p-1">
                        <div class="col-4">
                            <span>Code</span>
                        </div>
                        <div class="col-8">
                            <p>{{ infoHt.code_ref }}</p>
                        </div>
                    </div>
                </div>


            </div>

            <div class="card-footer bg-transparent">
                <span>{{ infoHt.statut }}</span>
                <span class="mx-1 text-success">
                    <svg xmlns="http://www.w3.org/2000/svg" width="1.5em" height="1.5em" fill="currentColor"
                        class="bi bi-check-lg" viewBox="0 0 16 16">
                        <path
                            d="M12.736 3.97a.733.733 0 0 1 1.047 0c.286.289.29.756.01 1.05L7.88 12.01a.733.733 0 0 1-1.065.02L3.217 8.384a.757.757 0 0 1 0-1.06.733.733 0 0 1 1.047 0l3.052 3.093 5.4-6.425a.247.247 0 0 1 .02-.022Z" />
                    </svg>
                </span>
            </div>
        </div>
    </div>

</template>


<script setup>

import { ref, computed, onMounted, onUnmounted } from "vue";
import { createPopper } from '@popperjs/core';
import REGIONS from '@/assets/regions.json'


const props = defineProps({
    typesColors: {
        types: Object,
    }
});

const selectedColor = ref("#ceceec");
const showPopover = ref(false);
let pInstance = null;
let virtualElement = null;
let popper = ref();
const infoHt = ref({});


const appt = computed(() => {
    const a = infoHt.value.appt;
    return _2digits(a);
});
const bloc = computed(() => {
    const a = infoHt.value.bloc;
    return _2digits(a);
});
const etage = computed(() => {
    const a = infoHt.value.etage;
    return _2digits(a);
});

function _2digits(num) {
    const prefix = (num <= 9) ? "0" : "";
    return prefix + num;
}


function setPopperBody(region) {
    infoHt.value = region;
}

function initPopover() {

    virtualElement = {
        getBoundingClientRect: generateGetBoundingClientRect(),
    };

    popper = document.getElementById('popper');

    pInstance = createPopper(virtualElement, popper, {
        placement: 'top',
        modifiers: [
            {
                name: 'offset',
                options: {
                    offset: ({ placement }) => {
                        if (placement === 'bottom') {
                            return [0, 30];
                        }

                        if (placement === 'top') {
                            return [0, 30];
                        }

                        return [];
                    },
                }
            },
        ]
    });
}

function updatePopover(x, y) {
    virtualElement.getBoundingClientRect = generateGetBoundingClientRect(x, y);
    pInstance.update();
}

function generateGetBoundingClientRect(x = 0, y = 0) {
    return () => ({
        width: 0,
        height: 0,
        top: y,
        right: x,
        bottom: y,
        left: x,
    });
}

function onHSHover(e) {
    const detail = e.detail;
    const region = detail.region;
    if (region === undefined) return;

    showPopover.value = true;

    const pos = e.detail.mousePos;
    updatePopover(pos.x, pos.y);

    const regionBody = REGIONS[region.name];

    const type = regionBody.type;
    selectedColor.value = props.typesColors[type];
    setPopperBody(regionBody);
}

function onHSHoverOut() {
    showPopover.value = false;
}

onMounted(() => {
    initPopover();
    window.addEventListener("OnHSHover", onHSHover);
    window.addEventListener("OnHSHoverOut", onHSHoverOut);
});

onUnmounted(() => {
    window.removeEventListener("OnHSHover", onHSHover);
    window.removeEventListener("OnHSHoverOut", onHSHoverOut);
});

</script>




<style lang="scss" scoped>
#popper {
    opacity: .9;
    z-index: 1031;
    width: 20em;
}

.card {
    $type-color : v-bind(selectedColor);
    border-radius: .3em;

    .card-header {
        .color-type {
            height: .5em;
            background-color: $type-color;
