        span {
            font-size: .9em;
            font-weight: bold;
            color: $type-color;
        }

        p {
            font-size: .9em;
            color: #7f7f7f;
            margin: 0;
            font-weight: 500;
        }
    }

    .card-footer {
        span {
            font-size: .8em;
